.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.faculty-glyphic-regular {
  font-family: "Faculty Glyphic", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.fade-in {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.fade-out {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.bg-green { 
  background: linear-gradient(
          rgba(0, 0, 0, 0.7), 
          rgba(0, 0, 0, 0.7)
        ), url('./assests/art-institute-of-chicago-duYq9UmcORk-unsplash.jpg');
}

.bg-red {
  background: linear-gradient (
          rgba(0, 0, 0, 0.7), 
          rgba(0, 0, 0, 0.7)
        ), url('./assests/birmingham-museums-trust-wKlHsooRVbg-unsplash.jpg');
}

.bg-black {
  background: linear-gradient (
          rgba(0, 0, 0, 0.7), 
          rgba(0, 0, 0, 0.7)
        ), url('./assests/chloe-lam-I4ScSrKsfIg-unsplash.jpg');
}
.bg-blue {
  background: linear-gradient (
          rgba(0, 0, 0, 0.7), 
          rgba(0, 0, 0, 0.7)
        ), url('./assests/catherine-kay-greenup-l5EaYAbV1X4-unsplash.jpg');
}

.bg-white {
  background: linear-gradient (
          rgba(0, 0, 0, 0.7), 
          rgba(0, 0, 0, 0.7)
        ), url('./assests/chloe-lam-I4ScSrKsfIg-unsplash.jpg');
}
.bg-default{background-color: #282c34;}
